import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions} from 'react-native';

export const stylesResponsive = () =>{

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;
    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
            container:{
                width: Dimensions.get('window').width,
                height: Dimensions.get('window').height,
                alignItems:'center',
                justifyContent:'center',
                backgroundColor:'rgba(0,0,0,0.7)'
            },
            content:{
                padding: isMobile?Dimensions.get('window').width/25:Dimensions.get('window').width/50,
                width:  isMobile?Dimensions.get('window').width/1.1:Dimensions.get('window').width/2.5,
                backgroundColor:'#fff',
                borderRadius:  isMobile?Dimensions.get('window').width/60:Dimensions.get('window').width/150,
            },
            title:{
                fontSize: isMobile?Dimensions.get('window').width/20:Dimensions.get('window').width/55,
                marginBottom: isMobile?Dimensions.get('window').width/35:Dimensions.get('window').width/80,
            },
            message:{
                fontSize: isMobile?Dimensions.get('window').width/28:Dimensions.get('window').width/80,
                marginBottom: isMobile?Dimensions.get('window').width/18:Dimensions.get('window').width/50,
                paddingLeft: isMobile?Dimensions.get('window').width/25:Dimensions.get('window').width/60,
            },
            containerButton:{
                width: '100%',
                alignItems:'flex-end'
            },
            button:{
                width: isMobile?Dimensions.get('window').width/15:Dimensions.get('window').width/45,
            },
            textButton:{
                fontSize: isMobile?Dimensions.get('window').width/30:Dimensions.get('window').width/90,
                textAlign: 'center',
                fontWeight:'bold',
                color:'#00a7fa'

            },
    }

    ))
}