import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions} from 'react-native';

export const stylesResponsive = () =>{

    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
            container:{
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'center',
                marginVertical: '5%',
                width:  Dimensions.get('window').width/2
            },
            title:{
                fontSize: isMobile? Dimensions.get('window').width/40 :Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
                padding: isMobile? Dimensions.get('window').width/200: Dimensions.get('window').width/300,
            },
            titleResponse:{
                fontSize: isMobile? Dimensions.get('window').width/30 < 15? Dimensions.get('window').width/30: 15 :Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
                paddingHorizontal: isMobile? Dimensions.get('window').width/150: Dimensions.get('window').width/100,
                fontWeight: 'bold'
            },
            subtr:{
                fontWeight:'bold'
            },
            divider:{
                justifyContent:'center',
                alignItems:'center'
            },
            highlight:{
                borderBottomWidth: 1
            },
            highlightTop:{
                borderTopWidth: 1,
                marginLeft: 5
            },
            divided:{
                flexDirection:'row',
            },
            parentheses:{
                fontSize: isMobile? Dimensions.get('window').width/14: Dimensions.get('window').width/20,
            }
    }

    ))
}