import React from 'react';
import { View, Text, Modal, TouchableOpacity } from 'react-native';
import { useDataCalc } from '../../context/dataCalc';

import { stylesResponsive } from './styles.js';

const AlertModal = () => {
    const {isAlert, handleAlert, alert} = useDataCalc()
    const styles = stylesResponsive();
    return (
        <Modal visible={isAlert} transparent>
            <View style={styles.container}>
                <View style={styles.content}>
                    <Text style={styles.title}>{alert?.title}</Text>
                    <Text style={styles.message}>{alert?.message}</Text>
                    <View style={styles.containerButton}>
                        <TouchableOpacity onPress={handleAlert} style={styles.button}>
                            <Text style={styles.textButton}>Ok</Text>
                        </TouchableOpacity>
                    </View>
                </View>
            </View>
        </Modal>
    );
}

export default AlertModal;