import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions} from 'react-native';

export const stylesResponsive = () =>{

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;
    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
            container:{
                alignItems: 'center',
                width:  isMobile?Dimensions.get('window').width:Dimensions.get('window').width/2.5,
                marginBottom: Dimensions.get('window').width/15
            },
            title:{
                fontSize: isMobile?Dimensions.get('window').width/22:Dimensions.get('window').width/45,
                marginBottom: Dimensions.get('window').width/75,
                marginTop: Dimensions.get('window').width/40,
                
            },
            dataForm:{
                justifyContent:'center',
                alignItems:'center',
                alignContent:'center',
            }
    }

    ))
}