import React,{createContext, useContext, useEffect, useState} from 'react'
import { Alert, Platform } from 'react-native'
import {floorRounding} from '../utils/round'
import {securityParameters} from '../utils/securityParameters'

const DataCalc = createContext({
    setHeightToCentroid:()=>{},
    setConcretMoment:()=>{},
    setHeight:()=>{},
    setBase:()=>{},
    setFyk:()=>{},
    setFck:()=>{},
    calculate:()=>{},
    isAlert: false,
    handleAlert: () =>{},
    alert:{},
    data:{},
    responses:{}
})

const DataCalcProvider = ({children})=>{

    //Data Initial
    const [heightToCentroid, setHeightToCentroid] = useState(0) //d'
    const [concreteMoment, setConcretMoment] = useState(0)//Mk
    const [base, setBase] = useState(0)//bw
    const [height, setHeight] = useState(0)//h
    const [fyk, setFyk] = useState(0)
    const [fck, setFck] = useState(0)
    const [isAlert, setIsAlert] = useState(false)
    

    //DataProcessing
    const [alert, setAlert] = useState({})

    let usefulHeight;//d
    let majorMoment;//Md
    let fyd;
    let fcd;
    let neutralLine;
    let normLimit;
    const [data, setData] = useState();
    const [responses, setResponses] = useState();

    const calculateMd = () => {
        let mk = concreteMoment<0?concreteMoment*-1:concreteMoment
        const md =  mk * securityParameters.momento
        return floorRounding(md);
    }
    const lessenFyk = () => {
        const force =  fyk / securityParameters.fyd;
        return floorRounding(force);
    }
    const lessenFck = () => {
        const force =  fck / securityParameters.fcd;
        return floorRounding(force);
    }

    const calculateUsefulHeight = () => {
        const response = height - heightToCentroid;
        return floorRounding(response);
    }

    const calculateNeutralLine = () => {
        let md = majorMoment<0?majorMoment*-1:majorMoment
        const response = 1.25 * usefulHeight*(1 - (Math.sqrt( 1 - (md/(0.425*base*(usefulHeight**2)*fcd)))))
        return floorRounding(response)
    }

    const calculateNormLimit = () => {
        const response = usefulHeight * securityParameters.normLimit;
        let message;

        if(neutralLine<=response){
            message = 'Armadura Simples'
        }
        else if(neutralLine>response){
            message = 'Armadura Dupla'
        }
        return {
            Xlimit: floorRounding(response),
            message
        }
    }

    const valideteForm=()=>{
        if(
            heightToCentroid == 0 ||
            concreteMoment == 0 ||
            base == 0 ||
            height == 0 ||
            fyk == 0 ||
            fck == 0 
        ){
            setIsAlert(true)
            let dataAlert={
                title:'Atenção',
                message: 'Preenxa todos os campos!!!'
            }

            setAlert(dataAlert)
            return;
        }
        else if(heightToCentroid>height){
            setIsAlert(true)
            let dataAlert={
                title:'Atenção',
                message: " d' não pode ser maior que h!!!"
            }

            setAlert(dataAlert)
            return;
        }
        else{
            calculate()
        }
        
    }

    const calculate = ()=>{

        majorMoment = calculateMd();
        fcd = lessenFck();
        fyd = lessenFyk();
        usefulHeight = calculateUsefulHeight();
        neutralLine = calculateNeutralLine();
        normLimit = calculateNormLimit()

        setData({
            heightToCentroid,
            Mk: concreteMoment<0?Math.abs(concreteMoment):concreteMoment,
            bw: base,
            h: height,
            fck,
            fyk
        })

        setResponses({
            fristStage:{
                Md: majorMoment,
                fcd,
                fyd,
                d: usefulHeight,
            },
            secundStage:{
                x: neutralLine
            },
            thirdStage:{
                ...normLimit
            }
            
        })

        return;
    }

    useEffect(()=>{
        console.log(data)
    },[data])
    
    useEffect(()=>{
        console.log(responses)
    },[responses])

    return(
        <DataCalc.Provider value={{
            setHeightToCentroid,
            setConcretMoment,
            setBase,
            setFyk,
            setFck,
            setHeight,
            calculate: valideteForm,
            alert,
            isAlert,
            handleAlert: () => setIsAlert(false),
            data,
            responses
        }}
        >
            {children}
        </DataCalc.Provider>
    )
}

const useDataCalc = () => {
    const context = useContext(DataCalc)

    if(!context){
        throw new Error('useDataCalc deve ser usado em um DataCalcProvider')
    }

    return context
} 

export {DataCalcProvider, useDataCalc}
