import React from 'react';
import { View, Text} from 'react-native';
import AlertModal from '../../components/AlertModal';
import {InitialData} from '../../components/InitialData';
import { NeutralLine } from '../../components/NeutralLine';
import { NormLimit } from '../../components/NormLimit';
import { TreatmentOfForces } from '../../components/TreatmentOfForces';
import { useDataCalc } from '../../context/dataCalc';

import { stylesResponsive } from './styles.js';

const Calculator = () => {
    const {responses} = useDataCalc()
    const styles = stylesResponsive();
    return (
        <View style={styles.container}>
            <AlertModal/>
            <Text style={styles.title}>1º Trabalhando os dados</Text>
            <InitialData/>
            {
                responses?
                    <>
                        <TreatmentOfForces/>
                        <Text style={styles.title}>2º Calculando a linha neutra</Text>
                        <NeutralLine/>
                        <Text style={styles.title}>3º Linha neutra vs limite da norma</Text>
                        <NormLimit/>
                    </>
                : null
            }
        </View>
    );
}

export default Calculator;