import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions, Platform} from 'react-native';

export const stylesResponsive = () =>{

    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
            container:{
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent:'center',
                marginVertical: '5%',
                maxWidth: 500,
                width:  Dimensions.get('window').width/2.5
            },
            title:{
                fontSize: isMobile?Dimensions.get('window').width/30 :Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
            },
            highlight:{
                borderBottomWidth: 1
            },
            input:{
                maxWidth: Dimensions.get('window').width/20,
                fontSize: Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
                borderWidth: 1,

            }
    }

    ))
}