import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions} from 'react-native';

export const stylesResponsive = () =>{

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;
    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
        container:{
            justifyContent:'center',
            alignItems:'center'
        },
        equation:{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            marginVertical: '5%',
            maxWidth: 600,
            width:  Dimensions.get('window').width/2.5
        },
        title:{
            fontSize: isMobile?Dimensions.get('window').width/30 :Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
            paddingHorizontal: isMobile?Dimensions.get('window').width/150:Dimensions.get('window').width/300,
        },
        limit:{
            fontSize: isMobile? Dimensions.get('window').width/50:Dimensions.get('window').width/100,
            paddingTop: isMobile?Dimensions.get('window').width/100:Dimensions.get('window').width/150
        },
        highlight:{
            borderBottomWidth: 1
        },
        message:{
            fontSize: isMobile?Dimensions.get('window').width/25:Dimensions.get('window').width/50,
            color: '#f00',
            fontWeight: 'bold'
        }
    }

    ))
}