import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions} from 'react-native';

export const stylesResponsive = () =>{

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;

    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
            container:{
                flexDirection: 'row',
                // alignItems: 'center',
                justifyContent: isMobile? 'flex-start':'center',
                marginBottom: '1%',
                maxWidth: isMobile? '100%': 300,
            },
            title:{
                fontSize: isMobile? Dimensions.get('window').width/23:Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
                width: isMobile ? Dimensions.get('window').width/9: Dimensions.get('window').width/25,
            },
            input:{
                width: isMobile? Dimensions.get('window').width/2.5: Dimensions.get('window').width/5,
                fontSize: isMobile? Dimensions.get('window').width/22:Dimensions.get('window').width/55 < 18? Dimensions.get('window').width/55: 18,
                borderBottomWidth: 1,

            }
    }

    ))
}