import React from 'react';
import { View, Text, TextInput } from 'react-native';

import {stylesResponsive} from './styles.js';
import {securityParameters} from '../../utils/securityParameters'
import { useDataCalc } from '../../context/dataCalc.js';

export const MdCalculate = ({name='Md', setdata=()=>{}}) => {
    const styles = stylesResponsive()
    const {data, responses} = useDataCalc()
    return (
        <View style={styles.container}>
            <Text style={styles.title}>{name} = </Text>
            <Text style={[styles.title, styles.highlight]}>{data.Mk}</Text>
            <Text style={styles.title}>  *  </Text>
            <Text style={styles.title}>{securityParameters.momento}</Text>
            <Text style={styles.title}>  =  </Text>
            <Text style={[styles.title, styles.highlight]}>{responses.fristStage.Md} KN/cm</Text>
        </View>
    );
}

