import react,{useEffect, useState} from 'react';
import {StyleSheet, Dimensions} from 'react-native';

export const stylesResponsive = () =>{

    const [dimensions, setDimensions] = useState(Dimensions.get('window'))
    const isMobile = Dimensions.get('window').width < Dimensions.get('window').height;

    useEffect(() => {
        const subscription = Dimensions.addEventListener('change', ({ window, screen }) => {
            
            setDimensions({ window, screen });
        });
        return () => subscription?.remove();
    },[]);

    return (StyleSheet.create({
            container:{
                justifyContent: isMobile ?'center':'space-between',
                alignItems: 'center',
                width:  Dimensions.get('window').width,
                marginBottom: Dimensions.get('window').width/15
            },
            title:{
                fontSize: isMobile?Dimensions.get('window').width/21:Dimensions.get('window').width/45,
                marginBottom: Dimensions.get('window').width/75
            },
            dataForm:{
                alignItems:'center',
            },
            button:{
                alignItems:'center',
                justifyContent:'center',
                marginTop: isMobile? Dimensions.get('window').width/25: Dimensions.get('window').width/50,
                width:  isMobile? Dimensions.get('window').width/2:Dimensions.get('window').width/4.2,
                height:  isMobile? Dimensions.get('window').width/10 :Dimensions.get('window').width/25,
                borderRadius: Dimensions.get('window').width/250,
                backgroundColor:'#1DDE5B',
                borderWidth: Dimensions.get('window').width/400,
                borderColor: '#05E050'
            }, 
            textButton:{
                fontSize: isMobile?Dimensions.get('window').width/22:Dimensions.get('window').width/50,
                color: '#fff',
            }
    }

    ))
}